@import "../../libs/variables.scss";

.Friends {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    margin: 60px 20px;

    > * {
        text-align: center;
    }

    .__friendsList {
        display: flex;
        justify-content: center;
        margin: 60px 0;
        flex-wrap: wrap;
        width: 100%;
        height: 250px;
        border-bottom: 2px solid $main;
    
        overflow-x: hidden;
    
        > * {
            width: 300px;
            margin: 20px 0;
        }

        .__pagination {
            display: flex;
            justify-content: center;
            width: 100%;
        }
    }
}